.site-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 0;
  max-width: 1120px;
  margin: auto;
}

.footer-block-item {
  display: flex;
  height: auto;
  width: 25%;
  a:hover {
    text-decoration: underline;
  }
  i {
    margin-right: 0.5rem;
  }
  p {
    color: $font-light;
    font-family: $font__main;
    margin-bottom: 0;
    a {
      color: $font-light;
      font-family: $font__main;
    }
  }
  &:last-child {
    align-self: flex-start;
    justify-content: flex-end;
    padding-right: 0;
    width: 50%;
  }
  span .days-of-week {
    font-weight: 500;
  }
  .text-logo {
    max-width: 50%;
  }
}

h3 {
  display: flex;
  flex-direction: column;
  color: $color-header;
  text-transform: uppercase;
  font-family: $font__header-bold;
}

.copyright {
  text-transform: uppercase;
  color: $font-light;
  font-family: $font__header;
  text-align: center;
  padding: 1.5rem 0;
}
