/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/

/* Front page */

/* Jounal Entry Blog */
.content-area {
  border-right: $border;
  margin-right: 5%;
  width: 73%;
}
.content-area,
.widget-area {
  padding: 3rem 0;
}

.comments-link {
  a {
    color: $font-light;
  }
  &:hover {
    color: $hover-light;
  }
}

.entry-header {
  line-height: 0;
  margin-right: 5%;
  margin-top: 2.25rem;
  position: relative;
}

.entry-meta {
  background-color: $color__link;
  color: $font-light;
  text-transform: uppercase;
  padding: 1rem 1.5rem;
  position: absolute;
  bottom: 0;
  right: 0;
}

.entry-title {
  background-color: $color__link;
  color: $font-light;
  font-family: $font__header;
  font-size: 2.1rem;
  max-width: 75%;
  padding: 0.75rem 1.5rem;
  position: absolute;
  text-transform: uppercase;
  top: 0;
  a {
    // background-color: $color__link;
    color: $font-light;
    font-family: $font__header;
    text-decoration: none;
    &:hover {
      color: $hover-light;
    }
  }
}

.hentry {
  padding-right: 5%;
}

.site-content {
  display: flex;
  flex-direction: row;
  margin: auto;
  width: 1120px;
}

